html, body, .root  {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
    overflow-x: hidden;
    overflow-y: hidden;
    z-index: 0;

}
.field {
    position:relative;
    left:0px;
    top:0px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    width: 560px;
    height: 100%;
    max-height: 800px;
    min-height: 560px;
    border-bottom: 5px rgba(255, 255, 255, 0.2) dotted;
    border-top: 5px rgba(255, 255, 255, 0.2) dotted;
    border-left: 5px rgba(255, 255, 255, 0.575) dotted;
    border-right: 5px rgba(255, 255, 255, 0.575) dotted;
    background-color: transparent;
    z-index: 2;
}
canvas {
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

}
.upper {
    filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    -webkit-filter: blur(5px);
}

.pong {
    display:-webkit-inline-flex;
    display:inline-flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
    height: 100%; 
   
}
.background {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(top left, #6465A5,rgb(42, 161, 112));
    background-image: linear-gradient(to bottom right, #6465A5,rgb(42, 161, 112));
    z-index: 0;
    -webkit-transition: all ease-in-out 1s;
    transition: all ease-in-out 1s;
}
.background::after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    background-image: -webkit-linear-gradient(top left, #6465A5,rgb(13, 63, 92));
    background-image: linear-gradient(to bottom right, #6465A5,rgb(13, 63, 92));
    opacity: 0;
    -webkit-transition: all 3s ease-in-out;
    transition: all 3s ease-in-out;
}
 .started:after{
     opacity: 1;
 }
@media (max-width:320px)  { 
    /* smartphones, iPhone, portrait 480x320 phones */  
      .field {
        margin-top: 0;
        margin-right: 0;
        width: 100%;
        min-height: auto;
        height: 85%;

    }
    canvas {
        
        border-left: none;
        border-right: none;
    }
    .pong {
        display: -webkit-flex;
        display: flex;
        
    }
    .controls {
        width: 100%;
    }
    .background {
        background-image: -webkit-linear-gradient(top, #6465A5,rgb(42, 161, 112));
        background-image: linear-gradient(to bottom, #6465A5,rgb(42, 161, 112));
    }
    .background::after {
        background-image: -webkit-linear-gradient(top, #6465A5,rgb(13, 63, 92));
        background-image: linear-gradient(to bottom, #6465A5,rgb(13, 63, 92));
    }
}

@media (max-width:425px)  { 
    /* smartphones, iPhone, portrait 480x320 phones */  
      .field {
        margin-top: 0;
        width: 100%;
        min-height: auto;
        height: 80%;
        border-right: none;
        border-left: none;
    }
    .pong {
        display: block;
    }
    .controls {
        width: 100%;
    }
    canvas {
        border-left: none;
        border-right: none;
    }
    .background {
        background-image: -webkit-linear-gradient(top, #6465A5,rgb(42, 161, 112));
        background-image: linear-gradient(to bottom, #6465A5,rgb(42, 161, 112));
    }
    .background::after {
        background-image: -webkit-linear-gradient(top, #6465A5,rgb(13, 63, 92));
        background-image: linear-gradient(to bottom, #6465A5,rgb(13, 63, 92));
    }
}

.description {
    display: block;
    position: relative;
    right: 5rem;
    bottom: 10rem;
    justify-self: flex-start;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */
    z-index: 1;

}

.header {
    font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
    text-align: center;
    text-transform: uppercase;
    text-rendering: optimizeLegibility;
    color: white;
    font-size: 3rem;
    -webkit-transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    transition: all cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
    -webkit-user-select: none; /* Safari */        
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+/Edge */
    user-select: none; /* Standard */

}
.ctrl {
    font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
    text-align: left;
    text-rendering: optimizeLegibility;
    color: white;
    font-size: 1rem;  
}
.desc {
    font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
    font-weight: 900;
    color: white;
    font-size: 1em;
}
.scores {
    display: block;
    position: relative;
    left: 32rem;
    justify-self: flex-end;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    z-index: 1;
}
.scoredigit {
    font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
    font-weight: 900;
    color: white;
    font-size: 3rem;
    text-align: center;
    vertical-align: middle;
}
.fas {
    font-size: 1em;
}
.starter{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
}
.start {
     cursor: pointer;
     z-index: 4;
}
.start:hover {
    font-size: 3.5rem;
}
@media (max-width:320px)  { 
    /* smartphones, iPhone, portrait 480x320 phones */  
    .scores {
        left: 0;
    }
    .scoredigit {
        margin: 0;
        font-size: 1.5rem;
    }
}

@media (max-width:425px)  { 
    /* smartphones, iPhone, portrait 480x320 phones */  
    .scores {
        left: 0;
    }
    .scoredigit {
        margin: 0;
        font-size: 1.5rem;
    }
}

.controls {
    position: fixed;
    height: 21%;
    width: 560px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    overflow: auto;
    white-space: nowrap;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    z-index: 3;
    
}
button {
    font-size: 2rem;
    height: 6rem;
    width: 6rem;
    max-height: 30vw;
    max-width: 30vw;
    background-color:  rgba(0, 0, 0, 0.075);;
    background-repeat:no-repeat;
    color: white;
    margin-bottom: 0; 
    padding: 30px;
    border: none;
    text-align: center;
    text-decoration: none;
    border-radius: 50%;
    display: block;
    margin: auto;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}
button, .left {
   float: left;
   margin-left: 2vw;
}
button, .right {
    float: right;
    margin-right: 2vw;
 }
 :active, :hover, :focus {
    outline: 0;
    outline-offset: 10%;
}
button.active {
    background-color: rgba(0, 0, 0, 0.301);
}

@media (max-width:320px)  { 
    /* smartphones, iPhone, portrait 480x320 phones */  
    .controls {
        width: 100%;
    }
}

@media (max-width:425px)  { 
    /* smartphones, iPhone, portrait 480x320 phones */  
    .controls {
        width: 100%;
    }
}
